@import '~node_modules/modern-normalize/modern-normalize.css';
@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --main-background-color: #84a178;
  --main-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --accent-color: #f8d24e;
  --buttons-bgc: #0A8817;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: var(--main-background-color);
  color: var(--main-text-color);
  max-width: 1600px;
  margin: 0 auto;
}

.container {
  padding: 0 20px;
  width: 100%;
  margin: 0 auto;
}
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.section {
  padding: 60px 0;
}

.pretty-button {
  padding: 12px 34px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2857142857;
  color: #000;
  display: flex;
  align-items: center;
  text-align: center;
}

.pretty-button--fill {
  border: none;
  background-color: var(--accent-color);
}
.pretty-button--fill:hover {
  background-color: var(--buttons-bgc);
}
.pretty-button--fill:focus {
  background-color: var(--buttons-bgc);
  color: #fff;
}

.pretty-button--empty {
  border: solid 2px;
  border-color: var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
}
.pretty-button--empty:hover {
  border-color: var(--buttons-bgc);
}
.pretty-button--empty:focus {
  border-color: var(--buttons-bgc);
  color: #fff;
}

.not-pretty-button {
  padding: 0 0;
  display: flex;
  align-items: center;
  text-align: center;
  color: var(--accent-color);
  border: none;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  text-decoration-line: underline;
  background-color: rgba(0, 0, 0, 0);
}
@media screen and (min-width: 1200px) {
  .not-pretty-button {
    font-size: 14px;
    line-height: 1.2142857143;
  }
}
.not-pretty-button:hover {
  color: var(--buttons-bgc);
}
.not-pretty-button:focus {
  color: #fff;
}

.contacts-grid {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .contacts-grid {
    flex-direction: row;
    margin-bottom: 40px;
  }
}
@media screen and (min-width: 1200px) {
  .contacts-grid {
    gap: 32px;
    margin-bottom: 60px;
  }
}

.contacts-grid__item {
  width: 280px;
  padding-top: 40px;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 24px;
  border-radius: 24px;
  background-color: #fcfcfc;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
}
@media screen and (min-width: 768px) {
  .contacts-grid__item {
    flex-basis: calc((100% - 32px) / 3);
    padding-left: 24px;
    padding-right: 24px;
    padding-bottom: 40px;
  }
}
@media screen and (min-width: 1200px) {
  .contacts-grid__item {
    padding: 50px;
  }
}

.contacts__representation {
  display: inline-block;
  padding: 4px 22px;
  border-radius: 4px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.04em;
  color: var(--secondary-text-color);
  background-color: #f8d24e;
}
@media screen and (min-width: 768px) {
  .contacts__representation {
    margin-bottom: 32px;
  }
}

.contacts__adress {
  position: relative;
  margin-bottom: 48px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: var(--secondary-text-color);
}
@media screen and (min-width: 768px) {
  .contacts__adress {
    margin-bottom: 64px;
    padding-right: 30px;
  }
}
@media screen and (min-width: 1200px) {
  .contacts__adress {
    padding-right: 50px;
  }
}
.contacts__adress::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(24px);
  width: 100%;
  height: 1px;
  background-color: #e1e1e1;
}
@media screen and (min-width: 768px) {
  .contacts__adress::after {
    transform: translateY(32px);
  }
}

.contacts__adress-link {
  display: block;
  margin-top: 8px;
  line-height: 1.83;
  color: #d6936d;
}
@media screen and (min-width: 768px) {
  .contacts__adress-link {
    margin-top: 12px;
  }
}
.contacts__adress-link:hover, .contacts__adress-link:focus {
  text-decoration: underline;
}

.contacts__work-time {
  position: relative;
  margin-bottom: 48px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  color: var(--secondary-text-color);
}
@media screen and (min-width: 768px) {
  .contacts__work-time {
    margin-bottom: 64px;
  }
}
.contacts__work-time::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(24px);
  width: 100%;
  height: 0px;
  border: 1px solid #e1e1e1;
}
@media screen and (min-width: 768px) {
  .contacts__work-time::after {
    transform: translateY(32px);
  }
}
.contacts__work-time--time {
  display: block;
  color: #ababab;
}
.contacts__work-time--margin {
  margin-bottom: 8px;
}
@media screen and (min-width: 1200px) {
  .contacts__work-time--margin {
    margin-bottom: 12px;
  }
}

.contacts__link {
  display: block;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  color: var(--secondary-text-color);
}
.contacts__link:hover, .contacts__link:focus {
  color: var(--accent-color);
}
.contacts__link--tel {
  margin-bottom: 6px;
}
@media screen and (min-width: 768px) {
  .contacts__link--tel {
    margin-bottom: 4px;
  }
}

.pretty-button--contacts {
  min-width: 196px;
  cursor: pointer;
  outline: none;
}
.pretty-button--contacts:focus {
  fill: #ffffff;
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
@media screen and (min-width: 768px) {
  .button-container {
    flex-direction: row;
    justify-content: center;
  }
}

.button-icon {
  margin-left: 4px;
}